import React, { Component } from "react";
import { Switch, Route } from "react-router-dom";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { connect } from 'react-redux';
import Nav from '../Nav'
import { Link } from "react-router-dom";
import '../../Css/NewWrap.css'
import { actions } from "../../Redux/actions/Action";

class Configurator extends Component {
    constructor(props) {
        super(props);
        this.state = {
            header: "Create New Card",
            configurator: false,

        };
        this.saveCard = this.saveCard.bind(this);
    }

    setHeader = (header) => {
        this.setState({ header: header });
    }
    opendAndCloseAllCards = () => {
        this.setState({ configurator: !this.state.configurator })
    }
    saveCard(e) {
            this.props.handleSubmit(e);

        this.props.publishStatus === 1 ? this.props.updateCard(true) : this.props.addNewCurrentCard(true);
    }

    render() {
        return (
            <div className="container-fluid configurator_container">
                {/* header */}
                <header className="row configurator_header ">


                    <div className="col-1">
                        <Link to={this.state.configurator ? `/${this.props.userName}` : `/${this.props.userName}/AllCards`} >
                            <FontAwesomeIcon icon='cog' onClick={() => this.opendAndCloseAllCards()} color="#FFFF" />
                        </Link>

                    </div>
                    <div className="col configurator_header_title">{this.state.header}</div>
                    <div className="col-1 d-flex justify-content-end">
                        <FontAwesomeIcon icon='adjust' color="#FFFF" />
                    </div>
                </header>

                {/* contaner you component*/}
                <div className="row configurator_main ">
                    <Switch>
                        {/* {this.props.currentCard ?
                            <Route path={"/card" + this.props.uid}>
                                <Nav></Nav>
                            </Route> : ""
                        } */}
                        <Route path="/">
                            <Nav></Nav>
                        </Route>
                    </Switch>
                </div>

                {/* footer */}
                <footer className="row configurator_footer  ">
                    {/* mobile */}


                    <div className="col-5 d-flex justify-content-between align-items-center">
                        <FontAwesomeIcon icon={['fas', 'mobile-alt']} onClick={() => this.props.setScreenSize(0)} ></FontAwesomeIcon>
                        <FontAwesomeIcon icon={['fas', 'desktop']} onClick={() => this.props.setScreenSize(1)} color="#FFFF" />
                        <FontAwesomeIcon icon='eye' onClick={(e) => this.saveCard(e)} color="#FFFF" />

                    </div>

                    <div className="col d-flex justify-content-center align-items-center ">

                        <button className="publish rounded-pill m-1" onClick={(e) => {
                            this.props.handleSubmit(e);
                            this.props.publishStatus === 1 ? this.props.updateCard(false) : this.props.addNewCurrentCard(false) }
                        }>
                            <FontAwesomeIcon icon='caret-right' className="mr-2" color="#FFFF" />{this.props.publishStatus === 1 ? "Update" : "Publish"}
                        </button>
                    </div>
                </footer>


            </div>
        );
    }
}



const mapStateToProps = (state) => {
    return {
        currentCard: state.card.currentCard,
        publishStatus: state.card.publishStatus,
        userName: state.user.userName,
        validation: state.validation

    };
}
const mapDispatchToProps = (dispatch) => ({
    setScreenSize: (status) => dispatch(actions.setScreenSize(status)),
    addNewCurrentCard: (preview) => { return dispatch(actions.saveCard(preview)) },
    updateCard: (preview) => dispatch(actions.updateCard(preview)),
    handleSubmit: (event) => dispatch(actions.handleSubmit(event))

})

export default connect(mapStateToProps, mapDispatchToProps)(Configurator);







