import produce from 'immer';
import createReducer from "./reducerUtils";


const initialState = {
    formValid: true,
    errors: {
        fullName: '',
        rule: '',
        email: '',
        mobile: '',
        Whatsapp: '',
        // 
        Messenger: '',
        // 
        Facebook: '',
        Youtube: '',
        Instagram: '',
        Twitter: '',
        Linkedin: '',
        // 
        Slack: '',
        Dribbble: '',
        // 
        Waze: ''


        // 
    },
    validEmailRegex: RegExp(/^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i),
    validMobileRegex: RegExp(/^[0-9\b]+$/),
    validtextRegex: RegExp(/^[a-zA-Z]+$/),
    validFacebookRegex: RegExp(/(?:https?:\/\/)?(?:www\.)?facebook\.com\/.(?:(?:\w)*#!\/)?(?:pages\/)?(?:[\w\-]*\/)*([\w\-\.]*)/),
    validYoutubekRegex: RegExp(/^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|\&v=|\?v=)([^#\&\?]*).*/),
    validInstagramkRegex: RegExp(/^\s*(http\:\/\/)?instagram\.com\/[a-z\d-_]{1,255}\s*$/i),
    validTwitteramkRegex: RegExp('^(?:http://)?(?:www\\.)?twitter\\.com/(\\w+)$', 'i'),
    validLinkedinamkRegex: RegExp('^https?://((www|\w\w)\.)?linkedin.com/((in/[^/]+/?)|(pub/[^/]+/((\w|\d)+/?){3}))$'),
    validWazeamkRegex: RegExp(/^[MTWTF][AL][1-9]$/i),
};

const validateForm = (errors) => {
    let valid = true;
    Object.values(errors).forEach(
        (val) => val.length > 0 &&val===null && (valid = false)
    );
    return valid;
}
const valid = {
    handleChange(state, action) {
        action.payload.event.preventDefault();
        const { name, value } = action.payload.event.target;
        const textName = action.payload.textName;
        let errors = state.errors;
        debugger;
        switch (name) {
            case 'text':
                errors[textName] =
                    state.validtextRegex.test(value)
                        ? ''
                        : textName + ' is not valid!'
                break;
            case 'email':
                errors.email =
                    state.validEmailRegex.test(value)
                        ? ''
                        : 'Email is not valid!';
                break;
            case 'mobile' || 'Whatsapp':
                errors[name] =
                    state.validMobileRegex.test(value) && value.length === 10
                        ? ''
                        : 'Mobile is not valid!'
                break;
            case 'Facebook':
                errors.Facebook =
                    state.validFacebookRegex.test(value) || value.length === 0
                        ? ''
                        : 'Facebook is not valid!';
                break;
            case 'Whatsapp':
                errors[name] =
                    state.validMobileRegex.test(value) && value.length === 10 || value.length === 0
                        ? ''
                        : 'Whatsapp is not valid!'
                break;
            case 'Youtube':
                errors[name] =
                    state.validYoutubekRegex.test(value) || value.length === 0
                        ? ''
                        : 'Youtube is not valid!'
                break;
            case 'Instagram':
                errors[name] =
                    state.validInstagramkRegex.test(value) || value.length === 0
                        ? ''
                        : 'Instagram is not valid!'
                break;
            case 'Twitter':
                errors[name] =
                    state.validTwitteramkRegex.test(value) || value.length === 0
                        ? ''
                        : name + ' is not valid!'
                break;
            case 'Linkedin':
                errors[name] =
                    state.validLinkedinamkRegex.test(value) || value.length === 0
                        ? ''
                        : name + ' is not valid!'
                break;
            case 'Waze':
                errors[name] =
                    state.validWazeamkRegex.test(value) || value.length === 0
                        ? ''
                        : name + ' is not valid!'
                break;
            default:
                break;
        }
        errors = errors;
        // errors = { errors, [name]: value }
        // this.setState({ errors, [name]: value });

    },
    handleSubmit(state, action) {
        action.payload.preventDefault();
        if (validateForm(state.errors)) {
            state.formValid = true;
        } else {
            state.formValid = false;
        }
    }




};

export default produce((state, action) => createReducer(state, action, valid), initialState);