import React, { Component } from "react";

import '../../Css/Nav.css'

export default class IconShapStyle extends Component {
    constructor(props) {
        super(props);
        this.state = {

        };

    }

    render() {
        return (
            <div className="container-fluid mt-2 mb-2 " >
                <div className="row">
                    <label className="title">Icon Shap</label>
                </div>
                <div className="row d-flex justify-content-around">
                    <div className="col container" onClick={()=>this.props.changShapRadius("50%")}>
                        <label className="row title">Round</label>
                        <div className="row rounded-circle sizeBroder  bg-light"></div>
                    </div>


                    <div className="col container" onClick={()=>this.props.changShapRadius("0%")}>
                        <label className="row title">Square</label>
                        <div className="row rounded-0 sizeBroder bg-secondary"></div>
                    </div>

                    <div className="col container" onClick={()=>this.props.changShapRadius("20%")}>
                        <label className="row title">Radius</label>
                        <div className="row rounded  sizeBroder bg-secondary"></div>
                    </div>
                </div>

            </div >
        );
    }
}
