import { actions } from '../actions/Action';
import {newCard as nc} from '../../BL/NewCard'


export const getUid = ({ dispatch, getState }) => next => action => {

        if (action.type === 'UID_INIT') {
                var url = window.location;
                var userName = (url.pathname.split('/')[1]);
                return fetch('https://knowme.page/api/digitalcard/getUser/user/' + userName)
                        .then((res) => res.json())
                        .then((resJson) => {
                                dispatch(actions.setUserId(resJson.uid));
                                dispatch(actions.setUserName(userName));
                                dispatch({ type: 'CARD_INIT'});
                                return;
                        })
                        .catch((err) => {
                                console.log(err)
                        })
        }
        return next(action);
};

export const getCards = ({ dispatch, getState }) => next => action => {

        if (action.type === 'CARD_INIT') {
                var uid = getState().user.userId
                return fetch('https://knowme.page/api/digitalcard/' + uid + '?jwt?=123'
                ).then((res) => res.json())
                        .then((resJson) => {
                                dispatch(actions.setCards(resJson));
                        })
                        .catch((err) => {
                                console.log(err)
                        })
        }
        return next(action);
};


export const cardSelection = ({ dispatch, getState }) => next => action => {
        if (action.type === 'SET_CARD') {
                dispatch(actions.setCurrentCard(action.payload));
                var agentDetails = getState().card.currentCard.agentDetails;
                dispatch(actions.setcurrentAgentForCard(agentDetails));
                return;
        }
        return next(action);
};

export const createNewCard = ({ dispatch, getState }) => next => action => {
        if (action.type === 'SET_NEW_CARD') {
                dispatch(actions.setNewCurrentCard(action.payload));
                var agentDetails = getState().card.currentCard.agentDetails;
                dispatch(actions.setcurrentAgentForCard(agentDetails));
                return;
        }
        return next(action);
};

// add
export const saveNewCard = ({ dispatch, getState }) => next => action => {
        if (action.type === 'SAVE_CARD'&&getState().validation.formValid) {
                var agentDetails = getState().agentDetails.currentAgentForCard;
                var newCard = getState().card.currentCard;
                Object.assign(newCard["agentDetails"], agentDetails)
                var uid = getState().user.userId
                fetch('https://knowme.page/api/digitalcard/' + uid + '?jwt?=123', {
                        method: 'POST',
                        headers: {
                                Accept: 'application/json',
                                'Content-Type': 'application/json'
                        },
                        body: JSON.stringify(newCard)
                })
                        .then((res) => res.json()).then((resJson) => {
                                dispatch(actions.addNewCurrentCard(resJson));
                                dispatch(actions.setcurrentAgentForCard(resJson.agentDetails));
                                if (action.payload)//preview
                                        window.open("https://knowme.page/card/" + uid + "/" + resJson._id, "_blank")
                        })
                        .catch((err) => {
                                console.log(err)
                        })
        }
        return next(action);
};


//update
export const updateCard = ({ dispatch, getState }) => next => action => {

        if (action.type === 'UPDATE_CARD'&&getState().validation.formValid) {
                var agentDetails = getState().agentDetails.currentAgentForCard;
                var newCard = getState().card.currentCard;
                Object.assign(newCard["agentDetails"], agentDetails);
                var uid = getState().user.userId
                fetch('https://knowme.page/api/digitalcard/' + uid + '/' + newCard._id + '?jwt?=123', {
                        method: 'PUT',
                        headers: {
                                Accept: 'application/json',
                                'Content-Type': 'application/json'
                        },
                        body: JSON.stringify(newCard)
                })
                        .then((res) => res.json()).then((resJson) => {
                                dispatch(actions.updateCurrentCard(resJson));
                                dispatch(actions.setcurrentAgentForCard(resJson.agentDetails));
                                if (action.payload)//preview
                                        window.open("https://knowme.page/card/" + uid + "/" + resJson._id, "_blank")
                        })
                        .catch((err) => {
                                console.log(err)
                        })
                return;
        }
        return next(action);
};


// delete
export const deleteCard = ({ dispatch, getState }) => next => action => {
        if (action.type === 'DELETE_CARD') {
                var uid = getState().user.userId
                var newCard = getState().card.cards[action.payload];
                dispatch(actions.deleteNewCard(action.payload));

                fetch('https://knowme.page/api/digitalcard/' + uid + '/' + newCard._id + '?jwt?=123', {
                        method: 'POST',
                        headers: {
                                Accept: 'application/json',
                                'Content-Type': 'application/json'
                        },
                        body: JSON.stringify(newCard)
                })
                        .then((res) => res.json()).then((resJson) => {
                                dispatch(actions.setNewCard(nc())); 

                        })
                        .catch((err) => {
                                console.log(err)
                        })
                return;
        }
        return next(action);
};



