
// import fetch from "cross-fetch";
import React from "react";
import TextField from "@material-ui/core/TextField";
import Autocomplete from "@material-ui/lab/Autocomplete";
import CircularProgress from "@material-ui/core/CircularProgress";
import { makeStyles } from '@material-ui/core/styles';

import '../../Css/SelecComponent.css'
import { Input } from "@material-ui/core";

// function sleep(delay = 0) {
//     return new Promise((resolve) => {
//         setTimeout(resolve, delay);
//     });
// }

export default function SelecComponent() {
    const [open, setOpen] = React.useState(false);
    const [options, setOptions] = React.useState([]);
    const loading = open && options.length === 0;


    const useStyles = makeStyles((theme) => ({
        root: {
            '& .MuiInput-underline:after': {
                borderBottomColor: '#75798E',
            },
            '& .MuiInput-underline:before': {
                borderBottomColor: '#75798E',
            },
            '& .MuiInput-underline:hover:not(.MuiInput-disabled-23):not(.MuiInput-focused-22):not(.MuiInput-error-25):before':
            {
                borderBottom: "1px solid #75798E !important"
            },
            '& .MuiSvgIcon-root':
            {
                color:"#75798E"
            }
        },
        textFiled:
        {
            '& .MuiInputBase-input':
            {
                color: '#CFD1D9',
                fontSize: '0.8em'
            },
        },

    }));
    const classes = useStyles();
    React.useEffect(() => {
        let active = true;

        if (!loading) {
            return undefined;
        }


        (async () => {
            const response = await fetch(
                "https://knowme.page/api/digitalcard/"
            );
            const categories = await response.json();
            if (active) {
                var list = [];
                for (let x in categories) {
                    list = list.concat(categories[x].subCategory.map((val) => { return val; }))
                }
                setOptions(list);
            }
        })();

        return () => {
            active = false;
        };
    }, [loading]);

    // reset options if close
    React.useEffect(() => {
        if (!open) {
            setOptions([]);
        }
    }, [open]);


    return (

        <Autocomplete
            className={classes.root}
            id="asynchronous-demo"
            open={open}
            onOpen={() => {
                setOpen(true);
            }}
            onClose={() => {
                setOpen(false);
            }}
            getOptionSelected={(option, value) => option.description === value.description}
            getOptionLabel={(option) => option.description}

            options={options}
            loading={loading}
            renderInput={(params) => (
                <TextField
                    {...params}
                    placeholder="select category"
                    className={classes.textFiled}
                    InputProps={{
                        ...params.InputProps,
                        endAdornment: (
                            <React.Fragment>
                                {loading ? (
                                    <CircularProgress color="inherit" size={20} />
                                ) : null}
                                {params.InputProps.endAdornment}
                            </React.Fragment>
                        )
                    }}
                />
            )}
        />
    );
}
