import { createStore, combineReducers, applyMiddleware } from 'redux';
import { createNewCard,cardSelection,saveNewCard,updateCard,getCards,deleteCard,getUid } from '../MiddleWares/CardMiddleWare'
import agentDetails from '../reducers/AgentDetailsStore';
import card from '../reducers/CardStore'
import user from '../reducers/User'
import validation from '../reducers/Validation'




// const state = {
       
// };

const reducer = combineReducers({ agentDetails, card,user,validation });

const store = createStore(reducer, applyMiddleware(cardSelection,createNewCard,saveNewCard,deleteCard,updateCard,getCards,getUid));
window.store = store;
export default store;

store.dispatch({ type: 'UID_INIT'});
// store.dispatch({ type: "CARD_INIT" });

