import React, { Component } from "react";
import { connect } from 'react-redux';
import { Link } from "react-router-dom";
import { actions } from '../Redux/actions/Action';
import { newCard } from '../BL/NewCard'
import AddIcon from '@material-ui/icons/Add';
import '../Css/AllCards.css'



class AllCards extends Component {
    constructor(props) {
        super(props);
        this.state = {
        }

    };

    render() {
        return (     
//push
            this.props.cards?
            <div className="container-fluid" >
                <div className="row">
                        <Link className=" AddNew mt-3 ml-3 d-flex justify-content-center align-items-center " to={`/${this.props.userName}`}
                    onClick={() => this.props.createNewCard(newCard())}><AddIcon className="iconAdd"></AddIcon></Link>
                  
                        <Link to={`/${this.props.userName}`} className="col-3 mt-3 ">
                            <div className="row-3 cards  d-flex justify-content-center"></div>
                            <div className="row d-flex justify-content-center"></div>
                            </Link>

                    {/* <div className=" cards  mt-3 ml-3 d-flex justify-content-center align-items-center"></div> */}
                    {this.props.cards.map((card, i) => {
                        return <Link  key={i} to={`/${this.props.userName}`} onClick={() => this.props.fillCurrentCard(i)} className="col-3 mt-3 ">
                            <div className="row-3 cards  d-flex justify-content-center"></div>
                            <div className="row d-flex justify-content-center">{card.agentDetails.fullName ? card.agentDetails.fullName : "NoName"}</div>
                      
                         </Link>
                    })
                    }
                </div>

            </div>:""
        );
    }
}

const mapStateToProps = (state) => {
    return {
        cards: state.card.cards,
        userName:state.user.userName
    };
}

const mapDispatchToProps = (dispatch) => ({

    fillCurrentCard: (index) => dispatch(actions.setCard(index)),

    createNewCard: (newCard) => dispatch(actions.setNewCard(newCard))
})


export default connect(mapStateToProps, mapDispatchToProps)(AllCards);
